import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { EmployeeService } from 'src/app/service/employee.service';
import { ToasterService } from 'src/app/service/toaster.service';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Store } from '@ngrx/store';
import { employeeActionTypes } from 'src/app/store/employee.actions';

@Component({
  selector: 'app-delete-dialog',
  templateUrl: './delete-dialog.component.html',
  styleUrls: ['./delete-dialog.component.scss']
})
export class DeleteDialogComponent implements OnInit {

  title: string;
  message: string;
  value: any;
  id: number;
  result: any;
  dataSource: any;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(private store: Store, public dialogRef: MatDialogRef<DeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DeleteDialogModel, private empservice: EmployeeService, private toasterService: ToasterService) {
    // Update view with given values
    this.title = data.title;
    this.message = data.message;
    this.id = data.id
  }

  ngOnInit() {
  }

  getEmployeeDetails() {
    this.empservice.getEmployee().subscribe(res => {
      this.result = res;
      this.dataSource = new MatTableDataSource<Element>(this.result);
      this.dataSource.sort = this.sort;
    });
  }

  onConfirm(): void {    
    let employeeId = this.id;
    this.store.dispatch(employeeActionTypes.deleteEmployee({ employeeId }));
    this.getEmployeeDetails();
    this.dialogRef.close(true);
  }

  onDismiss(): void {
    this.dialogRef.close(false);
  }
}

/**
 * Class to represent confirm dialog model.
 *
 * It has been kept here to keep it as part of shared component.
 */
export class DeleteDialogModel {

  constructor(public title: string, public message: string, public id: number) {
  }
}
